<template>

    <el-menu
        class="common-side-bar"
        :default-active="$store.state.routerName"
        background-color="#304056"
        text-color="#BFCBD9"
        active-text-color="#318DF1"
        @select="select"
    >
        <el-menu-item index="Pressure" disabled>
            <i class="el-icon-s-home"></i>
            <span slot="title"></span>
        </el-menu-item>

        <el-submenu index="ProjectTest">
            <template slot="title">
                <i class="el-icon-menu"></i>
                <span slot="title"></span>
            </template>
            <el-menu-item index="ProjectList">
                <i class="el-icon-s-operation"></i>
                <span slot="title"></span>
            </el-menu-item>
            <el-menu-item index="ProjectDetail" :disabled="$store.state.routerName === 'ProjectList'">
                <i class="el-icon-s-order"></i>
                <span slot="title"></span>
            </el-menu-item>
        </el-submenu>

        <el-submenu index="ApiTest" :disabled="$store.state.routerName === 'ProjectList'">
            <template slot="title">
                <i class="el-icon-s-ticket"></i>
                <span slot="title"></span>
            </template>

            <el-menu-item-group>
                <el-menu-item v-for="item of side_ApiTest" :index="item.url" :key="item.url">
                    <span :class="item.code"></span>&nbsp;&nbsp;{{ item.name }}
                </el-menu-item>
            </el-menu-item-group>
        </el-submenu>
        <el-menu-item index="Pressure" disabled>
            <i class="el-icon-s-platform"></i>
            <span slot="title">UI </span>
        </el-menu-item>
        <el-submenu index="MOCK" :disabled="$store.state.routerName === 'ProjectList'">
            <template slot="title">
                <i class="el-icon-s-opportunity"></i>
                <span slot="title">MOCK</span>
            </template>
            <el-menu-item-group>
                <el-menu-item v-for="item of MOCK" :index="item.url" :key="item.url">
                    <span :class="item.code"></span>&nbsp;&nbsp;{{ item.name }}
                </el-menu-item>
            </el-menu-item-group>
        </el-submenu>

        <el-menu-item index="Register" disabled>
            <i class="el-icon-user-solid"></i>
            <span slot="title"></span>
        </el-menu-item>


    </el-menu>
</template>

<script>
export default {
    name: "Side",
    data() {
        return {
            side_ApiTest: [
                {name: "API ", url: "RecordApi", code: "el-icon-s-ticket"},
                {name: "API ", url: "AutoTest", code: "el-icon-s-finance"},
                {name: "", url: "RecordConfig", code: "el-icon-s-tools"},
                {name: "", url: "GlobalEnv", code: "el-icon-share"},
                {name: "", url: "HostIP", code: "el-icon-s-opportunity"},
                {name: "", url: "DebugTalk", code: "el-icon-s-help"},
                {name: "", url: "Task", code: "el-icon-s-promotion"},
                {name: "", url: "Reports", code: "el-icon-s-data"}
            ],
            MOCK: [
                {name: "", url: "MOCK", code: "el-icon-coordinate"}
            ],
        }
    },
    methods: {
        select(url) {
            this.$store.commit('setRouterName', url);
            this.$router.push({name: url});
            this.setLocalValue("routerName", url);
        }
    }
}
</script>

<style scoped>

.common-side-bar {
    position: fixed;
    top: 48px;
    border-right: 1px solid #ddd;
    height: 100%;
    width: 202px;
    background-color: #fff;
    display: inline-block;
}
</style>



